<template>
	<div class="backcont">
		<div id="pdfDom">
		   <!-- HTML, -->
		  <div class="meituan-content">
			<div class="login-cont">
				<div class="meituan-title">
					
				</div>
				<div class="meituan-user">
					<p></p>
					<el-input class="inptflex" type="text" v-model="user.account" placeholder="" clearable />
				</div>
				<div class="meituan-user">
					<p></p>
					<el-input class="inptflex"  show-password type="password" v-model="user.password" placeholder="" clearable />
				</div>
				<!--  -->
				<div class="reg-view" @click="xiugai"><p>{{user.regi}}</p></div>
				<!--  -->
				<el-checkbox style="margin-left:200px" v-model="checked" label="" @change="get" size="large" />
				<el-button :disabled="display" v-if="user.regi==''" @click="signin" type="primary" class="meituan-btn"></el-button>
				<el-button :disabled="display" v-else type="primary" @click="register" class="meituan-btn"></el-button>
			</div>
		</div>
		</div>
		<el-button type="primary" size="small" @click="getPdf('#pdfDom')" class="meituan-btn"></el-button>
	</div>
	
</template>

<script>
	import htmlToPdf from '../utils/htmlToPdf.js'
	import {ref,reactive,toRefs} from 'vue'
	import {ElMessageBox} from 'element-plus'
	import {getCurrentInstance} from 'vue'
	import { useRouter } from 'vue-router'
	export default{
		setup(){
			const htmlTitle=ref('111')
			const router = useRouter()
			const {proxy} = getCurrentInstance()
			const checked=ref(false)
			const display=ref(true)
			const user=reactive({
				account:"",
				password:"",
				regi:""
			})
			
			//
			const empty=()=>{
				user.account=''
				user.password=''
			}
			const get=(z)=>{
				console.log(z)
				if(z==false){
					display.value=true
				}else if(z==true){
					display.value=false
				}
			}
			//
			const signin=()=>{
				console.log(user.account)
				console.log(user.password)
				ElMessageBox.alert("fuck", '',{
					confirmButtonText: '',
					type:"error"
				}).then(res=>{
					
				}).catch(err=>{
					
				})
				empty()
			}
			//
			const register=async()=>{
				const obj = {
					account:user.account,
					password:user.password
				}
				const res = await new proxy.$request(proxy.$urls.m().register,obj).modepost()
				console.log(res)
				router.push('/datav')
				empty()
			}
			//
			const xiugai=()=>{
				if(user.regi==''){
					user.regi=''
					checked.value=false
					display.value=true
				}else if(user.regi==''){
					user.regi=''
					checked.value=false
					display.value=true
				}
			}
			return {
				user,signin,register,empty,checked,display,get,xiugai,router,htmlTitle
			}
		}
	}
</script>

<style scoped>
	.backcont{
		height: 100%;
	}
</style>
